
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
directives: {
TransferDom
},
components: {
Popup,
CheckIcon
},
data() {
return {
showPopup: false,
currentIndex:0,
checkList:[],
allChoose:false,
disabled:true,
nullDisabled:true,
certificateList: [],
hasCertificationList:[],//考试通过的数组
}
},
computed:{
totalPrice(){
let total = 0;
this.checkList.forEach( item => {
total+=item.examFee
});
return total;
}
},
methods: {
showPopupPage() {
this.showPopup = true;
},
log() {},
cancleBtn() {
this.showPopup = false;
},
selectCurrentPlayer(item,index){
this.disabled = false;
if(this.checkList.length === this.hasCertificationList.length){
this.allChoose = true;
}else{
this.allChoose = false;
}
},

selectAll(){
this.allChoose = !this.allChoose;
if(this.allChoose){
this.checkList = this.hasCertificationList;
this.disabled = false;
}else{
this.checkList =[];
this.disabled = true;
}
},
hanleApply(){

sessionStorage.setItem('checkList',JSON.stringify(this.checkList))
this.$router.push({name:'certificateDetails'})
}
},
created() {
let examPlanId = this.$route.query.examPlanId;
let examLevel = this.$route.query.examLevel;
let orderNo = this.$route.query.orderNo;
let params = {
examPlanId,
examLevel,
orderNo
};
this.$axios.get("/api/enroll/certificateApply", { params }).then(res => {
if (res.data.code === 0) {
console.log(res.data.data,'???')
this.certificateList = res.data.data;
this.hasCertificationList = this.certificateList.filter( e =>e.examResult == 0 && e.certificationType == 0)
if(this.hasCertificationList.length == 0){
this.nullDisabled = true;
}else{
this.nullDisabled = false;
}
}
});
}
};



